.padding {
	width: max-content;
	height: max-content;
	padding-left: 30px;
	padding-bottom: 30px;
}

.container {
	width: 395px;
	height: 287px;
	background: var(--color-friendCard);
	box-shadow: 7px 4px 14px rgba(49, 21, 4, 0.07);
	border: 1px solid rgba(245, 146, 86, 0.5);
	border-radius: 40px;
	padding: 15px;
	text-align: center;
}

.container h3 {
	font-weight: 700;
	font-size: 20px;
	line-height: 27px;
	text-decoration-line: underline;
	color: var(--btn-bg-another);
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: left;
	margin-top: 15px;
}
.logo {
	object-fit: contain;
	width: 100%;
}
.friendData {
	display: flex;
	flex-direction: column;
	justify-content: left;
	align-items: flex-start;
	padding-left: 15px;
	width: 65%;
	position: relative;
}
.friendData h6,
.friendData p {
	font-size: 18px;
	line-height: 18px;
	white-space: nowrap;
	text-align: left;
}
.friendData h6 {
	margin-bottom: 5px;
}
.friendData p {
	margin-bottom: 10px;
	width: 100%;
	text-overflow: ellipsis;
}
.image_container {
	width: 158px;
	height: 112px;
}
.popup {
	width: 120px;
	height: auto;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: nowrap;
	background: var(--bg-input-modal);
	border: 1px solid var(--btn-bg-another);
	box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.25);
	border-radius: 8px;
	padding: 10px;
	position: absolute;
	top: 50px;
}

.popup div {
	height: 100%;
	overflow: hidden;
}
.popup:first-child {
	width: 30%;
}
.popup:nth-child(2) {
	width: 70%;
}

.popup p {
	font-size: 12px;
	line-height: 16px;
}
#time {
	text-decoration: underline;
	cursor: pointer;
}
#adress {
	width: 90%;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: 18px;
	line-height: 18px;
	margin-bottom: 7px;
	white-space: nowrap;
	text-align: left;
	color: var(--color-text-friends);
	text-decoration: underline;
	cursor: pointer;
}
#adress:hover,
#time:hover {
	text-decoration: none;
}

@media screen and (max-width: 1300px) {
	.container {
		width: 336px;
		height: 246px;
	}
	.friendData h6,
	.friendData p,
	#adress,
	#time {
		font-size: 14px;
		line-height: 15px;
		display: flex;
	}
	.image_container {
		width: 100px;
	}
	.container h3 {
		font-size: 15px;
	}
	#adress {
		width: 170px;
	}
	.popup {
		top: 40px;
	}
	.popup p {
		font-size: 10px;
		line-height: 16px;
	}
}

@media screen and (max-width: 770px) {
	.flex {
		margin-top: 10px;
	}
	.popup {
		top: 30px;
	}
	#adress {
		width: 120px;
	}
	.container {
		width: 280px;
		height: 192px;
		padding: 0;
	}
	.padding {
		padding-left: 0;
	}
	.friendData h6,
	.friendData p,
	#adress,
	#time {
		font-size: 12px;
		line-height: 12px;
	}
	.image_container {
		width: 110px;
		height: 75px;
	}
	.container h3 {
		font-size: 12px;
	}
	.popup p {
		font-size: 9px;
		line-height: 10px;
	}
}
